.App {
  text-align: center;
}

.board {
  display: flex;
  justify-content: space-around;
  margin: 20px;
}

.free-cells,
.tableau {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.free-cell,
.pile {
  margin: 5px;
  padding: 10px;
  border: 1px solid black;
  min-width: 60px;
  min-height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  background-color: white;
  border: 1px solid black;
  width: 50px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
}
